.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
a h1:hover{
color: rgb(56, 243, 234);
transition: 0.4s;
}
.nav-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
}
.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 1rem
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.nav-menu li a:hover{
    color: aqua;
    transition: 0.4s;
}
.hamburger{
    display: none;
}
@media screen and (max-width:1000px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.4s;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
  }